import React, { Fragment, useEffect, useState } from "react";
import "./Products.css";
import { useSelector, useDispatch } from "react-redux";
import { clearErrors, getProduct } from "../../actions/productAction";
import Loader from "../layout/Loader/Loader";
import ProductCard from "../Home/ProductCard";
import Pagination from "react-js-pagination";
import Slider from "@material-ui/core/Slider";
import { useAlert } from "react-alert";
import Typography from "@material-ui/core/Typography";
import MetaData from "../layout/MetaData";
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import ReactButton from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const categories = [
  "Laptop",
  "Footwear",
  "Bottom",
  "Tops",
  "Attire",
  "Camera",
  "SmartPhones",
];

const Products = ({ match }) => {

  const dispatch = useDispatch();

  const alert = useAlert();

  const [currentPage, setCurrentPage] = useState(1);
  const [price, setPrice] = useState([0, 25000]);
  const [category, setCategory] = useState("");

  const [ratings, setRatings] = useState(0);

  const [showPrice, setShowPrice] = useState(false);
  const [showCategory, setShowCategory] = useState(false);
  const [showRating, setShowRating] = useState(false);

  const handlePriceClose = () => setShowPrice(false);
  const handlePriceShow = () => setShowPrice(true);

  const handleCategoryClose = () => setShowCategory(false);
  const handleCategoryShow = () => setShowCategory(true);

  const handleRatingClose = () => setShowRating(false);
  const handleRatingShow = () => setShowRating(true);

  const {
    products,
    loading,
    error,
    productsCount,
    resultPerPage,
    filteredProductsCount,
  } = useSelector((state) => state.products);

  const keyword = match.params.keyword;

  const setCurrentPageNo = (e) => {
    setCurrentPage(e);
  };

  const priceHandler = (event, newPrice) => {
    setPrice(newPrice);
  };
  let count = filteredProductsCount;

  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    dispatch(getProduct(keyword, currentPage, price, category, ratings));
  }, [dispatch, keyword, currentPage, price, category, ratings, alert, error]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <Fragment>
          <MetaData title="PRODUCTS -- ECOMMERCE" />

          <div className="filterBox">
            <ButtonGroup size="large" color="error" aria-label="large button group" className="filterDiv">
              <Button onClick={handlePriceShow}>Price</Button>
              <Button onClick={handleCategoryShow}>Category</Button>
              <Button onClick={handleRatingShow}>Ratings</Button>
            </ButtonGroup>

            <Modal show={showPrice} onHide={handlePriceClose}>
              <Modal.Header closeButton>
                <Modal.Title className="text-danger">Choose Product Price Range</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Typography>Price</Typography>
                <Slider
                  value={price}
                  onChange={priceHandler}
                  valueLabelDisplay="auto"
                  aria-labelledby="range-slider"
                  min={0}
                  max={25000}
                  color="secondary"
                />
              </Modal.Body>
              <Modal.Footer>
                <ReactButton variant="secondary" onClick={handlePriceClose}>
                  Close
                </ReactButton>
              </Modal.Footer>
            </Modal>

            <Modal show={showCategory} onHide={handleCategoryClose}>
              <Modal.Header closeButton>
                <Modal.Title className="text-danger">Select Category</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Typography>Categories</Typography>
                <ul className="categoryBox">
                  {categories.map((category) => (
                    <li
                      className="category-link"
                      key={category}
                      onClick={() => setCategory(category)}
                    >
                      {category}
                    </li>
                  ))}
                </ul>
              </Modal.Body>
              <Modal.Footer>
                <ReactButton variant="secondary" onClick={handleCategoryClose}>
                  Close
                </ReactButton>
              </Modal.Footer>
            </Modal>

            <Modal show={showRating} onHide={handleRatingClose}>
              <Modal.Header closeButton>
                <Modal.Title className="text-danger">Search Ratings</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <fieldset>
                  <Typography component="legend">Ratings Above</Typography>
                  <Slider
                    value={ratings}
                    onChange={(e, newRating) => {
                      setRatings(newRating);
                    }}
                    aria-labelledby="continuous-slider"
                    valueLabelDisplay="auto"
                    min={0}
                    max={5}
                    color="secondary"
                  />
                </fieldset>
              </Modal.Body>
              <Modal.Footer>
                <ReactButton variant="secondary" onClick={handleRatingClose}>
                  Close
                </ReactButton>
              </Modal.Footer>
            </Modal>
          </div>



          <div className="productDiv">
            <h2 className="productsHeading">Products</h2>

            <div className="products">
              {products &&
                products.map((product) => (
                  <ProductCard key={product._id} product={product} />
                ))}
            </div>


            {resultPerPage < count && (
              <div className="paginationBox">
                <Pagination
                  activePage={currentPage}
                  itemsCountPerPage={resultPerPage}
                  totalItemsCount={productsCount}
                  onChange={setCurrentPageNo}
                  nextPageText="Next"
                  prevPageText="Prev"
                  firstPageText="1st"
                  lastPageText="Last"
                  itemClass="page-item"
                  linkClass="page-link"
                  activeClass="pageItemActive"
                  activeLinkClass="pageLinkActive"
                />
              </div>
            )}
          </div>
        </Fragment>
      )
      }
    </Fragment >
  );
};

export default Products;
