import React, { Fragment } from "react";
import CheckoutSteps from "../Cart/CheckoutSteps";
import { useSelector } from "react-redux";
import MetaData from "../layout/MetaData";
import "./ConfirmOrder.css";
import { Link } from "react-router-dom";
import { Typography } from "@material-ui/core";
import { Button } from "@mui/material";

const ConfirmOrder = ({ history }) => {
  const { shippingInfo, cartItems } = useSelector((state) => state.cart);
  const { user } = useSelector((state) => state.user);

  const subtotal = cartItems.reduce(
    (acc, item) => acc + item.quantity * item.price,
    0
  );

  const shippingCharges = subtotal > 1000 ? 0 : 200;

  const tax = subtotal * 0.18;

  const totalPrice = subtotal + tax + shippingCharges;

  const address = `${shippingInfo.address}, ${shippingInfo.city}, ${shippingInfo.state}, ${shippingInfo.pinCode}, ${shippingInfo.country}`;

  const proceedToPayment = () => {
    const data = {
      subtotal,
      shippingCharges,
      tax,
      totalPrice,
    };

    sessionStorage.setItem("orderInfo", JSON.stringify(data));

    history.push("/process/payment");
  };

  return (
    <Fragment>
      <MetaData title="Confirm Order" />
      <div className="confirmOrderPage">
        <CheckoutSteps activeStep={1} />
        <div className="confirmForm">
          <div>
            <div className="confirmshippingArea">
              <h5>Shipping Info</h5>
              <div className="confirmshippingAreaBox">
                <div>
                  <p>Name: <span>{user.name}</span></p>
                </div>
                <div>
                  <p>Phone: <span>{shippingInfo.phoneNo}</span></p>
                </div>
                <div>
                  <p>Address: <span>{address}</span></p>
                </div>
              </div>
            </div>
            <div className="confirmCartItems">
              <h5>Your Cart Items:</h5>
              <div className="confirmCartItemsContainer">
                {cartItems &&
                  cartItems.map((item) => (
                    <div key={item.product}>
                      <img src={item.image} alt="Product" />
                      <Link to={`/product/${item.product}`}>
                        {item.name}
                      </Link>{" "}
                      <span>
                        {item.quantity} X {item.price} ={" "}
                        <b>{item.price * item.quantity}</b>
                      </span>
                    </div>
                  ))}
              </div>
            </div>
          </div>
          {/*  */}
          <div>
            <div className="orderSummary">
              <h5>Order Summery</h5>
              <div>
                <div>
                  <b><p>Subtotal: <span>{subtotal}</span></p></b>
                </div>
                <div>
                  <p>Shipping Charges: <span>{shippingCharges}</span></p>
                </div>
                <div>
                  <b><p>GST: <span>{tax}</span></p></b>
                </div>
              </div>

              <div className="orderSummaryTotal">
                <p>
                  <b>Total: <span>{totalPrice}</span></b>
                </p>
              </div>

              <Button onClick={proceedToPayment} variant="contained" color="success">Proceed To Payment</Button>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default ConfirmOrder;
