import React from 'react';
import { useDispatch } from 'react-redux';
import { NavLink, useHistory } from 'react-router-dom';
import { logout } from "../../../actions/userAction";
import { useAlert } from "react-alert";

const Header2 = ({ user }) => {

    const history = useHistory();
    const dispatch = useDispatch();
    const alert = useAlert();

    const options = [
        { name: "Search", link: "/search" },
        { name: "Cart", link: "/cart" },
        { name: "Profile", link: "/account" },
        { name: "Orders", link: "/orders" }
    ];

    if (user.role === 'admin') {
        options.unshift({
            name: "Dashboard",
            link: "/admin/dashboard"
        });
    }

    function logoutUser() {
        dispatch(logout());
        alert.success("Logout Successfully");
    }

    return (
        <nav className="navbar navbar-expand-lg darkBGColor navbarPosition" data-bs-theme="dark">
            <div className="container-fluid">
                <NavLink className="navbar-brand text-danger" to="/">AmazKart</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarText">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <NavLink className="nav-link" aria-current="page" to="/">Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/products">Products</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/contact">Contact</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/about">About</NavLink>
                        </li>
                    </ul>
                    <span className="navbar-text">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            {options.map((item) => (
                                <li className="nav-item">
                                    <NavLink className="nav-link text-warning" to={item.link} key={item.name}>{item.name}</NavLink>
                                </li>
                            ))}
                            <button className='nav-link text-warning bg-black border border-0' onClick={logoutUser}>Logout</button>
                        </ul>
                    </span>
                </div>
            </div>
        </nav>
    )
}

export default Header2;