import React, { Fragment, useState } from "react";
import "./Shipping.css";
import { useSelector, useDispatch } from "react-redux";
import { saveShippingInfo } from "../../actions/cartAction";
import MetaData from "../layout/MetaData";
import PinDropIcon from "@material-ui/icons/PinDrop";
import HomeIcon from "@material-ui/icons/Home";
import LocationCityIcon from "@material-ui/icons/LocationCity";
import PublicIcon from "@material-ui/icons/Public";
import PhoneIcon from "@material-ui/icons/Phone";
import TransferWithinAStationIcon from "@material-ui/icons/TransferWithinAStation";
import { Country, State } from "country-state-city";
import { useAlert } from "react-alert";
import CheckoutSteps from "../Cart/CheckoutSteps";
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';

const Shipping = ({ history }) => {
  const dispatch = useDispatch();
  const alert = useAlert();
  const { shippingInfo } = useSelector((state) => state.cart);

  const [address, setAddress] = useState(shippingInfo.address);
  const [city, setCity] = useState(shippingInfo.city);
  const [state, setState] = useState(shippingInfo.state);
  const [country, setCountry] = useState(shippingInfo.country);
  const [pinCode, setPinCode] = useState(shippingInfo.pinCode);
  const [phoneNo, setPhoneNo] = useState(shippingInfo.phoneNo);

  const shippingSubmit = (e) => {
    e.preventDefault();

    if (phoneNo.length < 10 || phoneNo.length > 10) {
      alert.error("Phone Number should be 10 digits Long");
      return;
    }
    dispatch(
      saveShippingInfo({ address, city, state, country, pinCode, phoneNo })
    );
    history.push("/order/confirm");
  };

  return (
    <Fragment>
      <MetaData title="Shipping Details" />


      <div className="shippingContainer">
        <CheckoutSteps activeStep={0} />
        <div className="shippingBox">
          <h2 className="shippingHeading">Shipping Details</h2>

          <form
            className="shippingForm"
            encType="multipart/form-data"
            onSubmit={shippingSubmit}
          >
            <div>
              <TextField type="text" label="Address" variant="outlined" required value={address} onChange={(e) => setAddress(e.target.value)} color="success" />
            </div>

            <div>
              <TextField type="text" label="City" variant="outlined" required value={city} onChange={(e) => setCity(e.target.value)} color="success" />
            </div>

            <div>
              <TextField type="number" label="Pin Code" variant="outlined" required value={pinCode} onChange={(e) => setPinCode(e.target.value)} color="success" />
            </div>

            <div>
              <TextField type="number" label='Phone Number' variant="outlined" required value={phoneNo} onChange={(e) => setPhoneNo(e.target.value)} size="10" color="success" />
            </div>

            <div className="d-flex">
              <div className="m-2">
                <InputLabel>Country <PublicIcon /></InputLabel>
                <Select
                  required
                  value={country}
                  label="Country"
                  onChange={(e) => setCountry(e.target.value)}
                  color="success"
                >
                  {Country && Country.getAllCountries().map((item) => (
                    <MenuItem key={item.isoCode} value={item.isoCode}>{item.name}</MenuItem>
                  ))}
                </Select>
              </div>

              {country && (
                <div className="m-2">
                  <InputLabel>Country <TransferWithinAStationIcon /></InputLabel>
                  <Select
                    required
                    value={state}
                    label="State"
                    onChange={(e) => setState(e.target.value)}
                    color="success"
                  >
                    {State && State.getStatesOfCountry(country).map((item) => (
                      <MenuItem key={item.isoCode} value={item.isoCode}>{item.name}</MenuItem>
                    ))}
                  </Select>
                </div>
              )}
            </div>
            <Button type="submit" disabled={state ? false : true} variant="contained" color="success">Continue</Button>
          </form>
        </div>
      </div>
    </Fragment>
  );
};

export default Shipping;
