import React, { Fragment, useEffect } from "react";
import "./orderDetails.css";
import { useSelector, useDispatch } from "react-redux";
import MetaData from "../layout/MetaData";
import { Link } from "react-router-dom";
import { Typography } from "@material-ui/core";
import { getOrderDetails, clearErrors } from "../../actions/orderAction";
import Loader from "../layout/Loader/Loader";
import { useAlert } from "react-alert";
import { IoCheckmarkDoneCircle } from 'react-icons/io5';
import { NotPaid, Paid } from "./PaidOrNot";

const OrderDetails = ({ match }) => {
  const { order, error, loading } = useSelector((state) => state.orderDetails);

  const dispatch = useDispatch();
  const alert = useAlert();

  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    dispatch(getOrderDetails(match.params.id));
  }, [dispatch, alert, error, match.params.id]);



  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <Fragment>
          <MetaData title="Order Details" />
          <div className="orderDetailsPage">
            <div className="orderDetailsContainer">
              <h3>
                Order #{order && order._id}
              </h3>
              <h5>Shipping Info</h5>
              <div className="orderDetailsContainerBox">
                <div>
                  <p>Name: {order.user && order.user.name}</p>
                </div>
                <div>
                  <p>Phone: {order.shippingInfo && order.shippingInfo.phoneNo}</p>
                </div>
                <div>
                  <p>Address: {order.shippingInfo &&
                    `${order.shippingInfo.address}, ${order.shippingInfo.city}, ${order.shippingInfo.state}, ${order.shippingInfo.pinCode}, ${order.shippingInfo.country}`}</p>
                </div>
              </div>
              <h5>Payment Status</h5>
              <div className="orderDetailsContainerBox">
                <div>
                  <p>Amount: {order.totalPrice && order.totalPrice}</p>
                </div>
                <div>
                  <p
                    className={
                      order.paymentInfo &&
                        order.paymentInfo.status === "succeeded"
                        ? "greenColor"
                        : "redColor"
                    }
                  >
                    {order.paymentInfo &&
                      order.paymentInfo.status === "succeeded"
                      ? <Paid />
                      : <NotPaid />}
                  </p>
                </div>
              </div>

              <h5>Order Status</h5>
              <div className="orderDetailsContainerBox">
                <div>
                  <p
                    className={
                      order.orderStatus && order.orderStatus === "Delivered"
                        ? "greenColor"
                        : "redColor"
                    }
                  >
                    {order.orderStatus && order.orderStatus}
                  </p>
                </div>
              </div>
            </div>

            <div className="orderDetailsCartItems">
              <h5>Order Items:</h5>
              <div className="orderDetailsCartItemsContainer">
                {order.orderItems &&
                  order.orderItems.map((item) => (
                    <div key={item.product}>
                      <img src={item.image} alt="Product" />
                      <Link to={`/product/${item.product}`}>
                        {item.name}
                      </Link>{" "}
                      <span>
                        {item.quantity} X {item.price} ={" "}
                        <b>{item.price * item.quantity} </b>
                        + 18% GST
                      </span>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

export default OrderDetails;
